import { isValidSize } from './aslService';

export const getImageMaxSize = (
  width: number,
  height: number,
  maxWidth: number,
) => {
  if (
    isValidSize(width, height) && width > maxWidth) {
    return {
      width: maxWidth,
      height: maxWidth / width * height,
    };
  }

  return {
    width,
    height,
  };
};
